.keyboard-button-inner {
  display: flex;
  width: 29px;
  aspect-ratio: 1;
  /* flex: 1; */
  /* margin: 5px; */
  /* align-self: center; */
  padding: 0 0 0 0;
  /* margin: 14px 0 14px 0; */
  justify-content: center;
  align-items: center;
  /* background-color: #dddddd; */
  /* border-radius: 4px 4px 0 0; */
  border-radius: 50%;
}

.keyboard-button.wide {
  flex: 0 1 calc(15% - 6px);
  background-color: var(--unexplored-bg);
  justify-content: center;
  align-items: center;
  font-size: 10pt;
}

.keyboard-explainer-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  margin: 5px 0;
  /* background-color: green; */
}

.keyboard-explainer-letter-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  flex: 0;
  padding: 0 20px;
}

.keyboard-explainer-letter-wrapper .keyboard-button {
  flex: 0 1 100%;
  width: 38px;
  height: 58px;
}

.keyboard-explainer-text {
  flex: 1;
  text-align: left;
}

.bottomElement {
  display: flex;
  flex: 1;
}

.modal.hide {
  display: none;
}

.modal.show, .modal .shade {
  position: fixed;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  /* background-color: rgba(0, 0, 0, 0.5); */
  background-color: var(--main-bg);
  z-index: 2;
}

.modal.rules.show {
  justify-content: center;
  background: none;
}

.modal.rules .shade {
  background-color: rgba(0, 0, 0, 0.5);
}

.modal.rules.show .main-column {
  background-color: var(--header-secondary-color);
  width: 90vw;
  max-width: 450px;
  min-height: calc(100vh - 10vw);
  height: calc(100vh - 50px);
  border-radius: 10px;
  overflow: auto;
  z-index: 3;
}

.modal-content {
  padding: 0 8px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex: 1;
  /* font-size: 14pt; */
  /* grid-gap: 20px; */
}

.modal-content p {
  text-align: left;
  margin: 0.5em 0;
  padding: 0 5px;
}

.settings-row {
  flex: 0;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  border-bottom: 1px solid var(--rule-color);
  padding: 10px 0px;
}

.spacer {
  flex: 1;
}

.version-row {
  flex: 0;
  justify-self: flex-end;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  padding: 10px 0px;
}

.version-row .left {
  flex: 1;
  text-align: left;
}

.version-row .right {
  flex: 1;
  text-align: right;
}

