  align-items: center;
  border-bottom: 1px solid var(--rule-color);
  padding: 10px 0px;
} */

.settings-row.continues {
  border-bottom: none;
  /* padding-bottom: 0; */
}

.settings-row.continued {
  border: 1px solid var(--rule-color);
  border-radius: 8px;
  padding: 10px;
  margin: 0 20px;
}

.game-over-row {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  padding: 14px 0px;
}

.big-button {
  flex: 1;
  margin: 0 5%;
  height: 36px;
  /* border: 1px solid #dddddd; */
  border: 0;
  background-color: var(--unexplored-bg);
  border-radius: 8px;
  font-size: 14px;
  color: var(--unexplored-text-color);
  /* font-weight: bold; */
}

.big-button.share-success {
  color: white;
  background-color: var(--hit-bg);
  font-weight: bold;
}

.big-button.share-error {
  color: var(--past-square-text-color);
  background-color: var(--give-up-bg);
}


.daily-selector {
  display: flex;
  align-self: stretch;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  font-size: 10pt;
  font-weight: bold;
  letter-spacing: 0.75em;
}

.daily-selector-title {
  flex: 3;
}

.daily-option {
  flex: 1;
  padding: 0 2px;
  margin: 0 5px;
  height: 24px;
}

/* .daily-option.left {
  text-align: right;
}

.daily-option.right {
  text-align: left;
