@media screen and (max-width: 350px) {
  .keyboard-button.wide {
    font-size: 8pt;
  }
}

.settings-row label {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: center;
  flex: 1;
  gap: 20px;
}

.settings-row .setting-label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex: 1;
  text-align: left;
}

.setting-name {
  font-size: 16px;
  font-weight: bold;
}

.setting-description {
  /* display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start; */
  font-size: 10pt;
}

.settings-row .switch {
  display: flex;
  flex: 0;
}

.settings-row a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 0;
}

a {
  color: var(--link-color);
}

.number-input {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  flex: 0 100px;
  height: 30px;
}

.number-input button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 30px;
  /* font-weight: bold; */
  background-color: var(--unexplored-bg);
  color: var(--unexplored-text-color);
  border: 0;
  border-radius: 4px;
  font-size: 24px;
  padding: 0;
}

.number-input button.off {
  /* visibility: hidden; */
  background-color: var(--keyboard-off-bg);
  color: var(--keyboard-off-text-color);
}

.number-input .number-input-value {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 40px;
}

/* begin generated style */

.keyboard-button.major-major {
  --split-0-2: 0%;
  --split-1-2: 50%;
  --split-2-2: 100%;
}

.keyboard-button.major-minor {
  --split-0-2: 0%;
  --split-1-2: 80%;
  --split-2-2: 100%;
}

.keyboard-button.minor-major {
  --split-0-2: 0%;
  --split-1-2: 20%;
  --split-2-2: 100%;
}

.keyboard-button.minor-minor {
  --split-0-2: 0%;
  --split-1-2: 50%;
  --split-2-2: 100%;
}

.keyboard-button.major-major-major {
  --split-0-3: 0%;
  --split-1-3: 33%;
  --split-2-3: 66%;
  --split-3-3: 100%;
}

.keyboard-button.major-major-minor {
  --split-0-3: 0%;
  --split-1-3: 44%;
  --split-2-3: 88%;
  --split-3-3: 100%;
}

.keyboard-button.major-minor-major {
  --split-0-3: 0%;
  --split-1-3: 44%;
  --split-2-3: 55%;
  --split-3-3: 100%;
}

.keyboard-button.major-minor-minor {
  --split-0-3: 0%;
  --split-1-3: 66%;
  --split-2-3: 83%;
  --split-3-3: 100%;
}

.keyboard-button.minor-major-major {
  --split-0-3: 0%;
  --split-1-3: 11%;
  --split-2-3: 55%;
  --split-3-3: 100%;
}

.keyboard-button.minor-major-minor {
  --split-0-3: 0%;
  --split-1-3: 16%;
  --split-2-3: 83%;
  --split-3-3: 100%;
}

.keyboard-button.minor-minor-major {
  --split-0-3: 0%;
  --split-1-3: 16%;
  --split-2-3: 33%;
  --split-3-3: 100%;
}

.keyboard-button.minor-minor-minor {
  --split-0-3: 0%;
  --split-1-3: 33%;
  --split-2-3: 66%;
  --split-3-3: 100%;
}

.keyboard-button.hit-hit {
  background: linear-gradient(var(--keyboard-angle), var(--hit-bg) var(--split-0-2), var(--hit-bg) var(--split-1-2), var(--hit-bg) var(--split-1-2), var(--hit-bg) var(--split-2-2));
  color: var(--past-square-text-color);
}

.keyboard-button.hit-close {
  background: linear-gradient(var(--keyboard-angle), var(--hit-bg) var(--split-0-2), var(--hit-bg) var(--split-1-2), var(--close-bg) var(--split-1-2), var(--close-bg) var(--split-2-2));
  color: var(--past-square-text-color);
